import React, { useState, useRef } from 'react';
import { supabase } from '../../utils/supabaseClient';
import Spinner from '../../components/Spinner';
import { extractDocumentText } from '../../utils/visionAPI';
import { useAuth } from '../../contexts/AuthContext';
import { FaFileAlt, FaSearch, FaDownload, FaClipboard } from 'react-icons/fa';

const DocumentScanner = () => {
  const { user } = useAuth();
  const [file, setFile] = useState(null);
  const [preview, setPreview] = useState(null);
  const [loading, setLoading] = useState(false);
  const [results, setResults] = useState(null);
  const [error, setError] = useState(null);
  const [notification, setNotification] = useState(null);
  const [scanHistory, setScanHistory] = useState([]);
  const [fetchingHistory, setFetchingHistory] = useState(false);
  const fileInputRef = useRef(null);

  // Fetch scan history on component mount
  React.useEffect(() => {
    if (user) {
      fetchScanHistory();
    }
  }, [user]);

  const fetchScanHistory = async () => {
    if (!user) return;
    
    setFetchingHistory(true);
    try {
      const { data, error } = await supabase
        .from('document_scans')
        .select('*')
        .eq('user_id', user.id)
        .order('created_at', { ascending: false })
        .limit(5);
        
      if (error) throw error;
      setScanHistory(data || []);
    } catch (err) {
      console.error('Error fetching scan history:', err);
    } finally {
      setFetchingHistory(false);
    }
  };

  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    if (!selectedFile) return;
    
    // Check file type
    const validTypes = ['image/jpeg', 'image/png', 'image/jpg', 'application/pdf'];
    if (!validTypes.includes(selectedFile.type)) {
      setNotification({
        type: 'danger',
        message: 'Please upload a valid file (JPEG, PNG, or PDF)'
      });
      return;
    }
    
    setFile(selectedFile);
    
    // Create preview for images only
    if (selectedFile.type.startsWith('image/')) {
      setPreview(URL.createObjectURL(selectedFile));
    } else {
      setPreview(null);
    }
    
    setResults(null);
    setError(null);
  };

  const handleExtractText = async () => {
    if (!file) return;
    
    setLoading(true);
    setError(null);
    
    try {
      // Call the Vision API to extract text
      const data = await extractDocumentText(file);
      
      setResults(data);
      
      // Save to database
      if (user) {
        await saveToDatabase(data.text);
        // Refresh the scan history
        fetchScanHistory();
      }
      
      setNotification({
        type: 'success',
        message: 'Text successfully extracted from document'
      });
    } catch (err) {
      console.error('Error extracting text:', err);
      setError('Failed to extract text. Please try again with a different document.');
    } finally {
      setLoading(false);
    }
  };

  const saveToDatabase = async (extractedText) => {
    try {
      // Save to database
      const { error } = await supabase
        .from('document_scans')
        .insert({
          user_id: user.id,
          document_name: file.name,
          extracted_text: extractedText,
          created_at: new Date().toISOString()
        });
        
      if (error) throw error;
    } catch (dbError) {
      console.error('Error saving to database:', dbError);
      // Not showing this error to user since the text extraction was successful
    }
  };

  const handleDownload = () => {
    if (!results || !results.text) return;
    
    const blob = new Blob([results.text], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `extracted-text-${new Date().toISOString().slice(0, 10)}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleCopyToClipboard = () => {
    if (!results || !results.text) return;
    
    navigator.clipboard.writeText(results.text)
      .then(() => {
        setNotification({
          type: 'success',
          message: 'Text copied to clipboard'
        });
      })
      .catch((err) => {
        console.error('Failed to copy text:', err);
        setNotification({
          type: 'danger',
          message: 'Failed to copy text to clipboard'
        });
      });
  };

  const resetForm = () => {
    setFile(null);
    setPreview(null);
    setResults(null);
    setError(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  return (
    <div className="container py-4">
      {notification && (
        <div className={`alert alert-${notification.type} alert-dismissible fade show`}>
          {notification.message}
          <button 
            type="button" 
            className="btn-close" 
            onClick={() => setNotification(null)}
          ></button>
        </div>
      )}
      
      <h2 className="mb-4">
        <FaFileAlt className="me-2" />
        Medical Document Scanner
      </h2>
      <p className="text-muted mb-4">
        Upload medical documents, prescriptions, or reports to extract text using our AI-powered OCR system.
        The extracted text can be analyzed, saved, and referenced in your health records.
      </p>
      
      <div className="row">
        <div className="col-md-7">
          <div className="card shadow-sm mb-4">
            <div className="card-header">
              <h5 className="card-title mb-0">Upload Document</h5>
            </div>
            <div className="card-body">
              <div className="mb-3">
                <label htmlFor="documentUpload" className="form-label">Select Document</label>
                <input
                  type="file"
                  className="form-control"
                  id="documentUpload"
                  accept="image/jpeg,image/png,image/jpg,application/pdf"
                  onChange={handleFileChange}
                  ref={fileInputRef}
                />
                <small className="text-muted">Supported formats: JPG, PNG, PDF</small>
              </div>
              
              {preview && (
                <div className="mb-3 text-center">
                  <img 
                    src={preview} 
                    alt="Document Preview" 
                    className="img-fluid mb-2 border rounded"
                    style={{ maxHeight: '300px' }} 
                  />
                </div>
              )}
              
              {file && !preview && (
                <div className="mb-3 p-3 border rounded text-center">
                  <FaFileAlt size={30} className="mb-2 text-primary" />
                  <p className="mb-0">{file.name}</p>
                  <small className="text-muted">{(file.size / 1024).toFixed(2)} KB</small>
                </div>
              )}
              
              <div className="d-flex gap-2">
                <button 
                  className="btn btn-primary flex-grow-1" 
                  onClick={handleExtractText}
                  disabled={!file || loading}
                >
                  {loading ? (
                    <>
                      <Spinner size="sm" /> 
                      <span className="ms-2">Processing...</span>
                    </>
                  ) : (
                    <>
                      <FaSearch className="me-2" />
                      <span>Extract Text</span>
                    </>
                  )}
                </button>
                
                <button 
                  className="btn btn-outline-secondary" 
                  onClick={resetForm}
                  disabled={!file || loading}
                >
                  Reset
                </button>
              </div>
            </div>
          </div>
          
          {error && (
            <div className="alert alert-danger">{error}</div>
          )}
          
          {results && results.text && (
            <div className="card shadow-sm mb-4">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h5 className="card-title mb-0">Extracted Text</h5>
                <div className="btn-group">
                  <button 
                    className="btn btn-sm btn-outline-primary" 
                    onClick={handleCopyToClipboard}
                    title="Copy to clipboard"
                  >
                    <FaClipboard />
                  </button>
                  <button 
                    className="btn btn-sm btn-outline-primary" 
                    onClick={handleDownload}
                    title="Download as text file"
                  >
                    <FaDownload />
                  </button>
                </div>
              </div>
              <div className="card-body">
                <pre className="mb-0 extracted-text p-3 bg-light rounded" style={{
                  whiteSpace: 'pre-wrap',
                  maxHeight: '400px',
                  overflowY: 'auto'
                }}>
                  {results.text}
                </pre>
              </div>
            </div>
          )}
        </div>
        
        <div className="col-md-5">
          <div className="card shadow-sm">
            <div className="card-header">
              <h5 className="card-title mb-0">Recent Scans</h5>
            </div>
            <div className="card-body">
              {fetchingHistory ? (
                <div className="text-center py-3">
                  <Spinner />
                </div>
              ) : scanHistory.length > 0 ? (
                <div className="list-group">
                  {scanHistory.map((scan) => (
                    <div 
                      key={scan.id} 
                      className="list-group-item list-group-item-action"
                      onClick={() => {
                        setResults({ text: scan.extracted_text });
                        window.scrollTo({ top: 0, behavior: 'smooth' });
                      }}
                    >
                      <div className="d-flex w-100 justify-content-between">
                        <h6 className="mb-1 text-truncate" style={{ maxWidth: '70%' }}>
                          {scan.document_name}
                        </h6>
                        <small className="text-muted">
                          {new Date(scan.created_at).toLocaleDateString()}
                        </small>
                      </div>
                      <p className="mb-1 text-truncate">
                        {scan.extracted_text.substring(0, 50)}
                        {scan.extracted_text.length > 50 ? '...' : ''}
                      </p>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-center py-3 text-muted">
                  No scan history found. Upload a document to get started.
                </p>
              )}
            </div>
          </div>
          
          <div className="card shadow-sm mt-4">
            <div className="card-header">
              <h5 className="card-title mb-0">Tips</h5>
            </div>
            <div className="card-body">
              <ul className="mb-0">
                <li>For best results, ensure your document is well-lit and clearly visible</li>
                <li>Text extraction works best on typewritten documents</li>
                <li>Handwritten text may have varying accuracy depending on clarity</li>
                <li>You can save the extracted text to your health records for future reference</li>
                <li>Use the download button to save the text as a file</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DocumentScanner; 